___CSS_LOADER_EXPORT___.push([module.id, `/* src/pages/Reports/DosesAdministeredReportPage/DosesAdministeredReportPage.css */

.report-page-container {
    padding: var(--spacing-xl);
    background-color: var(--color-background-light);
    min-height: calc(100vh - var(--header-height) - var(--footer-height));
    color: var(--color-text-primary);
}

.report-header-section {
    display: flex;
    justify-content: space-between; /* Pushes title to left, button to right */
    align-items: center; /* Vertically aligns them */
    margin-bottom: var(--spacing-lg); /* Space below the header */
}

.report-header-section h2 {
    font-size: var(--font-size-h2);
    color: var(--color-primary-dark);
    margin: 0; /* Remove default H2 margin for clean alignment */
}

.report-controls {
    display: flex;
    align-items: center;
    gap: var(--spacing-md);
    margin-bottom: var(--spacing-lg);
    background-color: var(--color-white);
    padding: var(--spacing-md);
    border-radius: var(--border-radius-medium);
    box-shadow: var(--shadow-small);
}

.report-controls label {
    font-weight: 600;
    color: var(--color-text-primary);
}

.report-controls select {
    padding: var(--spacing-sm);
    border: 1px solid var(--color-medium-gray);
    border-radius: var(--border-radius-small);
    font-size: var(--font-size-base);
    background-color: var(--color-white);
    cursor: pointer;
    transition: border-color 0.3s ease;
}

.report-controls select:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 0 0 2px rgba(var(--color-primary-rgb), 0.2);
}

.report-content {
    background-color: var(--color-white);
    padding: var(--spacing-lg);
    border-radius: var(--border-radius-medium);
    box-shadow: var(--shadow-medium);
    margin-bottom: var(--spacing-xl);
}

.report-content h3 {
    margin-top: 0;
    margin-bottom: var(--spacing-md);
    color: var(--color-secondary-dark);
    border-bottom: 1px solid var(--color-light-gray);
    padding-bottom: var(--spacing-xs);
}

.report-status,
.report-error,
.no-data-message {
    text-align: center;
    padding: var(--spacing-lg);
    margin-top: var(--spacing-lg);
    background-color: var(--color-white);
    border-radius: var(--border-radius-medium);
    box-shadow: var(--shadow-small);
}

.report-error {
    color: var(--color-error);
    font-weight: 600;
}

.no-data-message {
    color: var(--color-text-secondary);
}`, "",{"version":3,"sources":["webpack://./src/pages/Reports/DosesAdministeredReportPage/DosesAdministeredReportPage.css"],"names":[],"mappings":"AAAA,kFAAkF;;AAElF;IACI,0BAA0B;IAC1B,+CAA+C;IAC/C,qEAAqE;IACrE,gCAAgC;AACpC;;AAEA;IACI,aAAa;IACb,8BAA8B,EAAE,0CAA0C;IAC1E,mBAAmB,EAAE,2BAA2B;IAChD,gCAAgC,EAAE,2BAA2B;AACjE;;AAEA;IACI,8BAA8B;IAC9B,gCAAgC;IAChC,SAAS,EAAE,iDAAiD;AAChE;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,gCAAgC;IAChC,oCAAoC;IACpC,0BAA0B;IAC1B,0CAA0C;IAC1C,+BAA+B;AACnC;;AAEA;IACI,gBAAgB;IAChB,gCAAgC;AACpC;;AAEA;IACI,0BAA0B;IAC1B,0CAA0C;IAC1C,yCAAyC;IACzC,gCAAgC;IAChC,oCAAoC;IACpC,eAAe;IACf,kCAAkC;AACtC;;AAEA;IACI,aAAa;IACb,kCAAkC;IAClC,yDAAyD;AAC7D;;AAEA;IACI,oCAAoC;IACpC,0BAA0B;IAC1B,0CAA0C;IAC1C,gCAAgC;IAChC,gCAAgC;AACpC;;AAEA;IACI,aAAa;IACb,gCAAgC;IAChC,kCAAkC;IAClC,gDAAgD;IAChD,iCAAiC;AACrC;;AAEA;;;IAGI,kBAAkB;IAClB,0BAA0B;IAC1B,6BAA6B;IAC7B,oCAAoC;IACpC,0CAA0C;IAC1C,+BAA+B;AACnC;;AAEA;IACI,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,kCAAkC;AACtC","sourcesContent":["/* src/pages/Reports/DosesAdministeredReportPage/DosesAdministeredReportPage.css */\n\n.report-page-container {\n    padding: var(--spacing-xl);\n    background-color: var(--color-background-light);\n    min-height: calc(100vh - var(--header-height) - var(--footer-height));\n    color: var(--color-text-primary);\n}\n\n.report-header-section {\n    display: flex;\n    justify-content: space-between; /* Pushes title to left, button to right */\n    align-items: center; /* Vertically aligns them */\n    margin-bottom: var(--spacing-lg); /* Space below the header */\n}\n\n.report-header-section h2 {\n    font-size: var(--font-size-h2);\n    color: var(--color-primary-dark);\n    margin: 0; /* Remove default H2 margin for clean alignment */\n}\n\n.report-controls {\n    display: flex;\n    align-items: center;\n    gap: var(--spacing-md);\n    margin-bottom: var(--spacing-lg);\n    background-color: var(--color-white);\n    padding: var(--spacing-md);\n    border-radius: var(--border-radius-medi